import React, { Component } from 'react';

//utils
import {Link} from "react-router-dom";

class Result extends Component{

    constructor(props){
        super(props);
        console.log(props);
        this.state = {
           product: {},
           socketServer:{},
           notify:{
               status: false,
               message:"",
               type:""
           }
        };

    }

    componentDidMount(){
      let {product} = this.props.location.state;
      let {socketServer} = this.props.location.state;
        this.setState({product,socketServer});
        console.log(this.state.socketServer);

        this.setState({socketServer: socketServer.onmessage = evt => {
            const message = JSON.parse(evt.data);
            let messageType = message.type;
            let messageData = message.data;

            if (messageType === "user:redeemed") {
                console.log("user redeemed", messageData);
                this.setState({
                notify: {
                    status: true,
                    message:messageData.message
                }
                });
              //end user redeemed  
            }

        }//end of messages
    });
        

    }


    render(){

        return (
            <div className="page midnightblue white-text flexy-center">


            <div className="flexy-center-div">
            <div className="alert alert-success ml-5" role="alert" hidden={!this.state.notify.status}>
         {this.state.notify.message}
   </div>
             
               <h1 className="white-text h1 text-center">Status: </h1>
               <h3 className="green-text h3 text-center">{this.state.product.name} redeemed</h3>
               <Link to="/">
               <button className="btn btn-lg btn-round btn-success btn-outline text-center">Done</button>
               </Link>
               

            </div>

        </div>
        );
    }

}

export default Result;