import React, { Component } from "react";

//utils
import {Link} from "react-router-dom";

//components
import { Select } from 'antd';
import { Button } from 'antd';

const { Option } = Select;

class Home extends Component {

    constructor(props) {
        super(props);
        this.state = {
            action: ""
        };
    }

    render() {

        function onChange(value) {
            console.log(`selected ${value}`);
            this.setState({action: value});

        }

        function onBlur() {
            console.log('blur');
        }

        function onFocus() {
            console.log('focus');
        }

        function onSearch(val) {
            console.log('search:', val);
        }

        return (
            <div className="page midnightblue white-text flexy-center">

                <div className="row">
                    <div className="col-sm-12 text-center">
                        <h1 className="sandybrown h1"> Welcome to Barvalley</h1>
                    </div>
                </div>
                <div className="flexy-center-div">
                    <p>
                        What do you want to do?
                      </p>

                    <Select
                        showSearch
                        style={{ width: 200 }}
                        placeholder="Choose Option"
                        optionFilterProp="children"
                        onChange={onChange.bind(this)}
                        onFocus={onFocus.bind(this)}
                        onBlur={onBlur.bind(this)}
                        onSearch={onSearch.bind(this)}
                        filterOption={(input, option) =>
                            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                        }
                    >
                        <Option value="balance">Check Balance</Option>
                        <Option value="redeem">Redeem</Option>
                        <Option value="top-up">Top-up</Option>
                    </Select>


                </div>
                <Link to={this.state.action}>
                    <Button type="primary" icon="download" className="btn-success mt-3" size="large"> Click to {this.state.action}</Button>
                </Link>
            </div>


        );
    }
}

export default Home;